<script>
import { ref } from "vue";
import { useRoute } from "vue-router";
import LoaderBar from "../components/LoaderBar.vue";
export default {
  name: "Seccion",
  components: { LoaderBar },

  setup(props) {
    const route = useRoute();

    var fecha = new Date();
    var mes = fecha.getMonth() + 1;
    var dia = fecha.getDate();
    var ano = fecha.getFullYear();
    if (dia < 10) dia = "0" + dia;
    if (mes < 10) mes = "0" + mes;
    const fechaFin = ref(ano + "-" + mes + "-" + dia);

    fecha.setDate(fecha.getDate() - 7);
    var mes = fecha.getMonth() + 1;
    var dia = fecha.getDate();
    var ano = fecha.getFullYear();
    if (dia < 10) dia = "0" + dia;
    if (mes < 10) mes = "0" + mes;
    const fechaInicio = ref(ano + "-" + mes + "-" + dia);

    // ARRAYS
    const arrayData = ref([]);
    const arraySecciones = ref([]);
    const arrayRanking = ref([]);
    const idUsuario = ref(localStorage.getItem("usuario"));
    const idDestino = ref(localStorage.getItem("idDestino"));
    const porcentaje = ref(1);

    const fetchDatos = async () => {
      try {
        let myTimer = setInterval(() => {
          porcentaje.value += 7;
          if (porcentaje.value >= 99) {
            porcentaje.value = 0;
          }
        }, 1000);

        const resp = await fetch(
          `../apis/reportes.php?action=reporteIncidenciasSubsecciones&idDestino=${idDestino.value}&idUsuario=${idUsuario.value}&fechaInicio=${fechaInicio.value}&fechaFin=${fechaFin.value}&idSeccion=${route.params.idSeccion}`
        );
        arrayData.value = await resp.json();
        clearInterval(myTimer);
        porcentaje.value = 100;
      } catch (error) {
        porcentaje.value = 100;
        console.log(error);
      }
    };
    fetchDatos();

    const ranking = async () => {
      try {
        const resp = await fetch(
          `../apis/reportes.php?action=rankingIncidenciasSubsecciones&idDestino=${idDestino.value}&idUsuario=${idUsuario.value}&fechaInicio=${fechaInicio.value}&fechaFin=${fechaFin.value}&idSeccion=${route.params.idSeccion}`
        );
        arrayRanking.value = await resp.json();
      } catch (error) {
        console.log(error);
      }
    };
    ranking();

    return {
      arrayData,
      arraySecciones,
      arrayRanking,
      fechaInicio,
      fechaFin,
      ranking,
      fetchDatos,
      porcentaje,
    };
  },
};
</script>

