<script>
import { ref } from "vue";
import LoaderBar from "../components/LoaderBar";
import { useRouter } from "vue-router";
export default {
  name: "Incidencias",
  components: { LoaderBar },
  setup(props) {
    const router = useRouter();
    var fecha = new Date();
    var mes = fecha.getMonth() + 1;
    var dia = fecha.getDate();
    var ano = fecha.getFullYear();
    if (dia < 10) dia = "0" + dia;
    if (mes < 10) mes = "0" + mes;
    const fechaFin = ref(ano + "-" + mes + "-" + dia);

    fecha.setDate(fecha.getDate() - 7);
    var mes = fecha.getMonth() + 1;
    var dia = fecha.getDate();
    var ano = fecha.getFullYear();
    if (dia < 10) dia = "0" + dia;
    if (mes < 10) mes = "0" + mes;
    const fechaInicio = ref(ano + "-" + mes + "-" + dia);

    // ARRAYS
    const idDestino = ref(localStorage.getItem("idDestino"));
    const idUsuario = ref(localStorage.getItem("usuario"));
    const arrayData = ref([]);
    const porcentaje = ref(0);
    const arrayCreadas = ref([]);
    const arraySolucionadas = ref([]);
    const arrayTiempo = ref([]);
    const arrayRatioCreadas = ref([]);
    const arrayRatioSolucionadas = ref([]);

    if (idDestino.value != 10) {
      router.push("/");
    }

    const fetchDatos = async () => {
      try {
        let myTimer = setInterval(() => {
          porcentaje.value += 7;
          if (porcentaje.value >= 99) {
            porcentaje.value = 0;
          }
        }, 1000);

        const res = await fetch(
          `../apis/reportes.php?action=reporteIncidencasGlobal&idDestino=${idDestino.value}&idUsuario=${idUsuario.value}&fechaInicio=${fechaInicio.value}&fechaFin=${fechaFin.value}`
        );
        arrayData.value = await res.json();

        // ORDENA LAS CREADAS
        arrayCreadas.value = arrayData.value.arrayCreadas.sort(function (a, b) {
          if (a.creadas > b.creadas) {
            return -1;
          }
        });

        // ORDENA LAS SOLUCIONADAS
        arraySolucionadas.value = arrayData.value.arraySolucionadas.sort(
          function (a, b) {
            if (a.solucionadas > b.solucionadas) {
              return -1;
            }
          }
        );

        // ORDENA LOS TIEMPO DE SOLUCIÓN
        arrayTiempo.value = arrayData.value.arrayTiempo.sort(function (a, b) {
          if (a.mediaSolucionadas < b.mediaSolucionadas) {
            return -1;
          }
        });

        // ORDENA POR RATIO DE CREADAS
        arrayRatioCreadas.value = arrayData.value.arrayRatioCreadas.sort(
          function (a, b) {
            if (a.ratioCreadas > b.ratioCreadas) {
              return -1;
            }
          }
        );

        // ORDENA POR RATIO DE SOLUCIONADAS
        arrayRatioSolucionadas.value =
          arrayData.value.arrayRatioSolucionadas.sort(function (a, b) {
            if (a.ratioSolucionadas > b.ratioSolucionadas) {
              return -1;
            }
          });

        clearInterval(myTimer);
        porcentaje.value = 100;
      } catch (error) {
        porcentaje.value = 100;
        console.log(error);
      }
    };
    fetchDatos();

    return {
      arrayData,
      fechaInicio,
      fechaFin,
      fetchDatos,
      idDestino,
      porcentaje,
      arrayCreadas,
      arraySolucionadas,
      arrayTiempo,
      arrayRatioCreadas,
      arrayRatioSolucionadas,
    };
  },
};
</script>

